.container-settings {
  z-index: 100;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  top: 0%;
  right: 0%;

  background-color: $secondary-clear2-color;
  min-height: 100%;
  width: 0;
  transform: translateX(100%);

  &.container-active {
    transition: transform 0.2s, width 0.2s;
    min-width: 150px;
    width: 90vw;
    max-width: 600px;
    transform: translateX(0);
  }

  .setting {
    position: absolute;
    top: 30px;
    left: -100px;
    z-index: 200;
    font-size: 35px;
    cursor: pointer;
    background: transparent;
    border: none;
    color: $secondary-clear1-color;
    &:active {
      box-shadow: none;
    }

    &.setting-active {
      left: 20px;
      color: $secondary-dark-color;
    }
  }

  .updates {
    margin-top: 30px;
    display: none;

    &.updates-active {
      width: 100%;
      display: block;
    }

    .interval-lap,
    .chronos {
      width: 100%;
      display: flex;
      justify-content: center;
      flex-wrap: wrap;

      .title-update {
        margin: 5px 10px;
        width: 110px;
        .title {
          font-weight: 700;
          margin-bottom: 5px;
          color: $primary-dark-color;
        }
        .update {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .num {
            padding: 5px;
          }
        }
      }
    }

    button.btn-setting {
      padding: 6px;
      min-width: 32px;
      font-size: 20px;
      border-radius: 10px;
      cursor: pointer;
      border: none;
      color: $primary-dark-color;
    }

    .ex {
      display: flex;
      flex-direction: column;
      min-width: 170px;
      width: 62%;
      margin: 10px auto;
      color: $primary-dark-color;

      .title {
        font-weight: 700;
      }
      .inputs {
        display: flex;
        flex-direction: column;
        input {
          height: 30px;
          margin: 5px 0;
          padding: 5px;
          outline: none;
          border: none;
          border-radius: 5px;
          font-family: "Comfortaa", cursive;
        }
      }
    }
  }
}

@media screen and (max-width: 440px) {
  .container-settings {
    .setting {
      left: -60px;
    }

    .updates {
      margin-top: 80px;
    }
  }
}
